<template>
    <div id="kc_school_info" style="transform:translateX(100%);transition:.3s;">
        <mt-header title="讲师信息" style="height:0.88rem;background: #4cc494;font-size:0.38rem;">
            <router-link to="/shop" slot="left">
                <mt-button icon="back"></mt-button>
            </router-link>
        </mt-header>
        <div>
            <div>
                <div v-for="i in arr" v-if="i.uid==$route.params.uid">
                    <div class="shop_kc_topa">
                        <div class="shop_top_img">
                            <img :src="i.img" alt="">
                        </div>
                        <div class="shop_top_word">
                            <h5 style="color:#000;">{{i.name}}</h5>
                            <div>
                                <span style="font-size:0.20rem;color:#868686;"><del style="font-size:0.26rem;">{{i.oldprice}}</del></span>
                                <span style="color:#fbc100;font-size:0.30rem;">{{i.newprice}}</span>
                            </div>
                        </div>
                    </div>
                    <div style="width:90%;height:0.8rem;line-height:0.8rem;border:0.01rem dotted #000;margin:0 auto;font-size:0.24rem;background:#e5e5e5;">
                        <div style="width:50%;box-sizing:border-box;border-right:0.01rem solid #ccc;float:left;text-align:center;">
                            <span class="iconfont" style="color:#4cc494;">&#xe6a9;</span>
                            购买人数:{{i.buy}}
                        </div>
                        <div style="width:50%;box-sizing:border-box;float:left;text-align:center;">
                            <span class="iconfont" style="color:#4cc494;">&#xe651;</span>
                            送雷豆:{{i.song}}
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
    export default {
        created() {
            setTimeout(()=>{
                var kc_school_info = document.querySelector('#kc_school_info');
                kc_school_info.style.transform = 'translateX(0)';
            },10)
        },
        data(){
            return {
                arr:[
                    {
                        uid:'school_1',
                        name:'美国TOP20名校高端文书',
                        oldprice:'原价￥41000',
                        newprice:'￥40000',
                        img:'http://i1.fuimg.com/699492/6c25da96661ed775.png',
                        buy:'223',
                        song:'20000'
                    },
                    {
                        uid:'school_2',
                        name:'美国TOP50名校高端文书',
                        oldprice:'原价￥32000',
                        newprice:'￥30000',
                        img:'http://i1.fuimg.com/699492/e8df767285516dc8.png',
                        buy:'112',
                        song:'10000'
                    },
                    {
                        uid:'school_3',
                        name:'美国G5名校高端文书',
                        oldprice:'原价￥21000',
                        newprice:'￥19999',
                        img:'http://i1.fuimg.com/699492/710038f7802967e2.png',
                        buy:'233',
                        song:'15000'
                    }
                ]
            }
        }
    }
</script>

<style>
    .shop_top_word{
        float:left;
        font-size: 0;
    }
    .shop_kc_topa{
		min-height: 1.79rem;
		width: 6.90rem;
		overflow: hidden;
		margin: 0 auto;
	}
    .shop_top_img{
		float: left;
		width:2.6rem;
		text-align: center;
		padding-top:0.15rem;
		box-sizing: border-box;
	}
	.shop_top_img img{
		width:2.30rem;
		height:1.47rem;
	}
    .shop_top_word h5{
		font-size: 0.31rem;
		line-height: 0.78rem;
		font-weight: normal;
	}
	.shop_top_word div{
		overflow: hidden;
		margin-top:0.15rem;
	}
</style>



