<template>
    <div id="mes" style="transform:translateX(100%);transition:.3s;">
        <div class="mes_head">
            <span class="iconfont" style="width:0.88rem;height:0.88rem;line-height:0.88rem;float:left;text-align:center;">
                <a href="#/" style="color:#fff;">&#xe74a;</a>
            </span>
            <div style="font-size:0.3rem;width:4.7rem;height:0.65rem;line-height:0.65rem;float:left;overflow:hidden;margin-top:0.1rem;border:0.02rem solid rgba(255,255,255,.5);border-radius:0.1rem;margin-left:0.42rem;">
                <span style="display:inline-block;width:49.1%;text-align:center;color:#fff;" @click="mes_index=0" :class="mes_index==0?'mes_active':''">系统消息</span>
                <span style="display:inline-block;width:49%;text-align:center;color:#fff;" @click="mes_index=1" :class="mes_index==1?'mes_active':''">活动提醒</span>
            </div>
        </div>
        <div>
            <div v-show="mes_index==0" class="mes_cont">
                暂无数据
            </div>
            <div v-show="mes_index==1" class="mes_cont">
                暂无数据
            </div>
        </div>
    </div>
</template>

<script>
    export default {
        data(){
            return {
                mes_index:0,
            }
        },
        created() {
            setTimeout(()=>{
                var mes = document.querySelector('#mes');
                mes.style.transform = 'translateX(0)';
            },10)
        },
    }
</script>

<style>
    .mes_head{
        height:0.88rem;
        background: #4cc494;
    }
    .mes_active{
        background: #fff;
        color:#4cc494 !important;
    }
    .mes_cont{
        min-height: 90vh;
        font-size: 0.38rem;
        color:#4cc494;
        text-align: center;
        line-height: 90vh;
    }
</style>


