<template>
    <div id="cir_act" style="transform:translateX(100%);transition:.3s;">
        <mt-header title="活动" style="height:0.88rem;background: #4cc494;font-size:0.38rem;">
            <router-link to="/circle" slot="left">
                <mt-button icon="back"></mt-button>
            </router-link>
        </mt-header>
        <div style="padding:0.4rem 0.3rem;">
            <div style="border-bottom:0.01rem solid #e9e9e9;overflow:hidden;padding-top:0.2rem;">
                <div class="act_icon">
                    <span class="iconfont" style="color:#ffa4b2;">&#xe606;</span>
                </div>
                <div style="float:left;">
                    <h4 class="act_name">高频单词团</h4>
                    <p class="act_info">High frequency word</p>
                </div>
                <div style="float:right;line-height:0.85rem;">
                    <span class="iconfont" style="font-size:0.6rem;vertical-align:middle;color:#a0a0a0;">&#xe625;</span>
                </div>
            </div>
            <div style="border-bottom:0.01rem solid #e9e9e9;overflow:hidden;padding-top:0.4rem;">
                <div class="act_icon">
                    <span class="iconfont" style="color:#f39a52;">&#xe636;</span>
                </div>
                <div style="float:left;">
                    <h4 class="act_name">听力联盟</h4>
                    <p class="act_info">hearing</p>
                </div>
                <div style="float:right;line-height:0.85rem;">
                    <span class="iconfont" style="font-size:0.6rem;vertical-align:middle;color:#a0a0a0;">&#xe625;</span>
                </div>
            </div>
            <div style="border-bottom:0.01rem solid #e9e9e9;overflow:hidden;padding-top:0.4rem;">
                <div class="act_icon">
                    <span class="iconfont" style="color:#ad79ff;">&#xe619;</span>
                </div>
                <div style="float:left;">
                    <h4 class="act_name">口语分队</h4>
                    <p class="act_info">spoken</p>
                </div>
                <div style="float:right;line-height:0.85rem;">
                    <span class="iconfont" style="font-size:0.6rem;vertical-align:middle;color:#a0a0a0;">&#xe625;</span>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
    export default {
        created() {
            setTimeout(()=>{
                var cir_act = document.querySelector('#cir_act');
                cir_act.style.transform = 'translateX(0)';
            },10)
        },
    }
</script>

<style>
    .act_icon{
        float: left;
        width:0.9rem;
        height:0.9rem;
        border-radius: 50%;
        text-align:center;
        line-height: 0.65rem;
        background-color: #fff;
        margin:0 0.2rem;
        border:0.01rem solid #f1f1f1;
        box-shadow: -0.02rem -0.02rem 0.02rem #ccc;
    }
    .act_icon span{
        font-size: 0.56rem;
        line-height: 0.56rem;
    }
    .act_name{
        font-size: 0.34rem;
        font-weight: normal;
        line-height: 0.6rem;
    }
    .act_info{
        font-size: 0.3rem;
        color:#6e6e6e;
        line-height: 0.4rem;
        margin-bottom: 0.4rem;
    }
</style>


