<template>
    <div id="kc_teacher_info" style="transform:translateX(100%);transition:.3s;">
        <mt-header title="讲师信息" style="height:0.88rem;background: #4cc494;font-size:0.38rem;">
            <router-link to="/shop" slot="left">
                <mt-button icon="back"></mt-button>
            </router-link>
        </mt-header>
        <div>
            <div>
                <div v-for="i in arr" v-if="i.uid == $route.params.uid">
                    <div style="background:#4cc480;overflow:hidden;">
                        <h4 style="font-size:0.44rem;text-align:center;font-weight:normal;color:#fff;line-height:0.7rem;">{{i.name}}</h4>
                        <div style="overflow:hidden;width:90%;margin:0.2rem auto;">
                            <div style="float:left;width:1.2rem;height:1.4rem;border-radius:50%;font-size:0;overflow:hidden;">
                                <img :src="i.img" alt="" style="width:100%;height:100%;">
                            </div>
                            <div style="width:75%;height:1.2rem;background:rgba(255,255,255,.4);float:left;font-size:0.24rem;color:#fff;">
                                <div style="text-align:center;width:33%;float:left;padding-top:0.15rem;">
                                    <p style="font-size:0.33rem;">{{i.num}}</p>
                                    关注人数
                                </div>
                                <div style="text-align:center;width:33%;float:left;padding-top:0.15rem;">
                                    <p style="font-size:0.33rem;">{{i.gfxy}}</p>
                                    高分学员
                                </div>
                                <div style="text-align:center;width:33%;float:left;padding-top:0.15rem;">
                                    <p style="font-size:0.33rem;">{{i.pj}}</p>
                                    评价数
                                </div>
                            </div>
                        </div>
                    </div>
                    <div style="font-size:0.34rem;line-height:0.5rem;padding:0.3rem;letter-spacing:0.01rem;">
                        {{i.info}}
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
    export default {
        created() {
            setTimeout(()=>{
                var kc_teacher_info = document.querySelector('#kc_teacher_info');
                kc_teacher_info.style.transform = 'translateX(0)';
            },10)
        },
        data(){
            return {
                arr:[
                    {
                        uid:'teacher_1',
                        img:'http://i1.fuimg.com/699492/4b250615c37c0828.png',
                        name:'Verna.Li',
                        info:'主讲四六级口语',
                        num:'133',
                        gfxy:0,
                        pj:0,
                        info:'海外留学多年，辅导过众多雅思7分以上的学员，能根据自身情况为学生制造符合学生背景的口语话题。课堂氛围轻松幽默'
                    },
                    {
                        uid:'teacher_2',
                        img:'http://i1.fuimg.com/699492/c705c593310474bf.png',
                        name:'Wendy',
                        info:'主讲听力教学',
                        num:'133',
                        gfxy:0,
                        pj:0,
                        info:'GMAT高分获得者,GMAT阅读授课零差评老师，持英语专业八级证书，英语基础扎实，对GMAT阅读有深入的研究和见解，拥有多年GMAT授课经验。授课风格轻松活泼，深入浅出。常年的英语工作环境，接触过不同国家的外籍人士，对海外国家的文化有独特见解。真诚，有责任感，有耐心。了解学生心理及学习状态，擅长根据学生的个人特点，指定个性化的考试复习方案，给学生提供专业的备考意见。'
                    }
                ]
            }
        }
    }
</script>

<style>

</style>



