<template>
    <div id="cir_group" style="transform:translateX(100%);transition:.3s;">
        <div>
            <div style="width:100vw;overflow:hidden;padding-bottom:0.6rem;">
                <img src="http://i1.fuimg.com/699492/cfb93e0760482209.png" alt="" style="width:100%;">
            </div>
            <div class="group_back">
                <a href="#/circle" class="iconfont">&#xe74a;</a>
            </div>
            <div class="group_btn">
                点击参与
            </div>
        </div>
    </div>
</template>

<script>
    export default {
        created() {
            setTimeout(()=>{
                var cir_group = document.querySelector('#cir_group');
                cir_group.style.transform = 'translateX(0)';
            },10)
        },
    }
</script>

<style>
    .group_back{
        width:0.6rem;
        height:0.6rem;
        position: fixed;
        top:0.4rem;
        left:0.35rem;
        background: rgba(0,0,0,.5);
        border-radius: 50%;
        line-height: 0.4rem;
        text-align: center;
    }
    .group_back a{
        font-size: 0.5rem;
        color:#fff;
        line-height:0.5rem;
    }
    .group_btn{
        width:100%;
        height:1.05rem;
        line-height: 1.05rem;
        color:#fff;
        font-size: 0.27rem;
        background: #ff6c8e;
        text-align: center;
        position:fixed;
        bottom:0;
    }
</style>


