<template>
    <div id="voice" style="transform:translateX(100%);transition:.3s;">
        <mt-header title="语音" style="height:0.94erm;background:#000;color:#fff;font-size:0.38rem;">
            <router-link to="/" slot="left">
                <mt-button icon="back" style="height:0.9rem;"></mt-button>
            </router-link>
        </mt-header>
        <div>
            <span class="iconfont" style="display:inline-block;font-size:3rem;margin:2.67rem 2.4rem;">
                &#xe6e1;
            </span>
        </div>
    </div>
</template>

<script>
    export default {
        created() {
            setTimeout(()=>{
                var voice = document.querySelector('#voice');
                voice.style.transform = 'translateX(0)';
            },10)
        },
    }
</script>

<style>
    
</style>


