<template>
	<div>
		<div>
			<ul class="rep_list">
				<li v-for="i in re_arr" :class="re_selected==i.path?'li_active':''">
					<a :href="'#/report/'+i.path" @click="re_selected = i.path"> {{i.title}} </a>
				</li>
			</ul>
			<div>
				<router-view></router-view>
			</div>
		</div>
		<div>
			<mynav></mynav>
		</div>
	</div>
</template>

<script>
	export default{
		data(){
			return {
				re_selected:'',
				re_arr:[
					{
						title:'背单词轨迹',
						path:''
					},
					{
						title:'单词报表',
						path:'re_form'
					},
					{
						title:'单词量评估',
						path:'re_assess'
					}
				]
			}
		},
		created() {
			console.log(this.$route.path)
		},
	}
</script>

<style>
	.rep_list{
		width: 100%;
		height:0.8rem;
		background: #4cc494;
		font-size: 0.34rem;
		display: flex;
	}
	.rep_list li{
		flex: 1;
		text-align: center;
		position: relative;
		line-height: 0.8rem;
	}
	.rep_list li a{
		color:#fff;
	}
	.rep_list .li_active::after{
		content: '';
		display:block;
		height:0.07rem;
		width: 2.2rem;
		background: #fff;
		position: absolute;
		bottom:0.07rem;
		left:0.15rem;
		border-radius: 0.03rem;
	}
</style>



