<template>
    <div id="alarm" style="transform:translateX(100%);transition:.3s;">
        <mt-header title="闹钟" style="height:0.88rem;background: #4cc494;font-size:0.38rem;">
            <router-link to="/own_center" slot="left">
                <mt-button icon="back"></mt-button>
            </router-link>
            <mt-button slot="right" style="font-size:0.6rem;">+</mt-button>
        </mt-header>
        <div style="min-height:90vh;font-size:0.28rem;color:#606060;line-height:90vh;text-align:Center;">
            您还没有学习闹钟，请添加学习闹钟
        </div>
    </div>
</template>

<script>
    export default {
         created() {
            setTimeout(()=>{
                var alarm = document.querySelector('#alarm');
                alarm.style.transform = 'translateX(0)';
            },10)
        }
    }
</script>

<style>

</style>



