<template>
    <div id="hh_english_book_more" style="transform:translateX(100%);transition:.3s;">
        <mt-header title="英语书本" style="height:0.88rem;background: #4cc494;font-size:0.38rem;">
            <router-link to="/shop" slot="left">
                <mt-button icon="back"></mt-button>
            </router-link>
        </mt-header>
        <div class="english_books">
            <div style="overflow:hidden;width:100%;">
                <div v-for="i in hh_english" class="english_gj">
                    <div>
                        <img :src="i.img" alt="">
                    </div>
                    <h4>{{i.name}}</h4>
                    <p>{{i.price}}</p>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
    export default {
        created() {
            setTimeout(()=>{
                var hh_english_book_more = document.querySelector('#hh_english_book_more');
                hh_english_book_more.style.transform = 'translateX(0)';
            },10)
        },
        data(){
            return {
                hh_english:[
					{
						img:'http://i1.fuimg.com/699492/9209ae03e530f72c.png',
						name:'道林格雷的画像',
						price:'￥37'
					},
					{
						img:'http://i1.fuimg.com/699492/5e285f5314c81bb1.png',
						name:'月亮和六便士',
						price:'39￥'
					},
					{
						img:'http://i1.fuimg.com/699492/eed8e51210b72ad0.png',
						name:'了不起的比尔盖茨',
						price:'￥39'
					}
				]
            }
        }
    }
</script>

<style>
    .english_books{
        width: 100%;
    }
	.english_books h3 span{
		float: right;
		font-size: 0.26rem;
	}
    .english_gj{
		width:3.42rem;
		float: left;
		margin:0.2rem 0.16rem;
	}
	.english_gj div{
		width:3.42rem;
		height:3.80rem;
		text-align: center;
		line-height: 3.80rem;
		background: #f2f2f2;
		border-radius: 0.3rem;
	} 
	.english_gj div img{
		width:2.75rem;
		height:3.13rem;
		margin-top: 0.35rem;
	}
	.english_gj h4{
		font-weight: normal;
		font-size: 0.34rem;
		line-height: 0.54rem;
		margin-top: 0.13rem;
	}
	.english_gj p{
		font-size: 0.26rem;
		color:#fb8542;
		line-height: 0.43rem;
		margin-bottom: 0.4rem;
	}
</style>


