<template>
    <mt-tabbar v-model="selected" fixed> 
        <mt-tab-item id="#/">
            <span slot="icon" class="iconfont" style="font-size:0.46rem;">&#xe621;</span>
            背单词
        </mt-tab-item>
        <mt-tab-item id="#/report">
            <span slot="icon" class="iconfont" style="font-size:0.46rem;">&#xe6f1;</span>
            单词报告
        </mt-tab-item>
        <mt-tab-item id="#/circle">
            <span slot="icon" class="iconfont" style="font-size:0.46rem;">&#xe69f;</span>
            圈子
        </mt-tab-item>
        <mt-tab-item id="#/shop">
            <span slot="icon" class="iconfont" style="font-size:0.46rem;">&#xe67c;</span>
            商城
        </mt-tab-item>
    </mt-tabbar>
</template>

<script>
    export default {
        data(){
            return {
                selected:'#/',
            }
        },
        watch:{
            selected(a,b){
                location.href = a
            }
        },
        created(){
            console.log(this.$route.path)
            this.selected = '#/' + this.$route.path.split('/')[1]
        }
    }
</script>

<style>
    .mint-tabbar{
        z-index: 999;
    }
    .mint-tabbar > .mint-tab-item.is-selected{
        color: #31c287;
        background: #ffffff;
    }
    .mint-tabbar{
        background: #ffffff;
        border-top: 1px solid #f5f5f5;
    }
</style>



