<template>
    <div id="rank" style="transform:translateX(100%);transition:.3s;">
        <mt-header title="评估月排行榜" style="height:0.88rem;background: #4cc494;font-size:0.38rem;">
            <router-link to=""  slot="left">
                <mt-button icon="back" @click="$router.go(-1)"></mt-button>
            </router-link>
            <mt-button slot="right" class="iconfont" @click="sheetVisible=true;">&#xe64e;</mt-button>
        </mt-header>
        <div class="cont">
            <div style="padding-bottom:1.25rem;">
                <div v-for="(i,$index) in arr" style="overflow:hidden;width:6.68rem;height:1.15rem;margin:0.12rem auto;background:#f2ece0;padding-top:0.24rem;padding-left:0.5rem;padding-right:0.25rem;box-sizing:border-box;">
                    <div style="float:left;width:0.7rem;height:0.7rem;border-radius:50%;overflow:hidden;font-size:0;">
                        <img src="http://img3.imgtn.bdimg.com/it/u=1107263072,1224997471&fm=26&gp=0.jpg" style="width:100%;height:100%;">
                    </div>
                    <div style="float:left;font-size:0.28rem;margin-left:0.2rem;color:#94928f;">
                        <p>{{i.name}}</p>
                        <p>词汇量：<span style="color:#4cc494;">{{i.num}}</span></p>
                    </div>
                    <div style="float:right;font-size:0.4rem;color:#4cc494;line-height:0.8rem;">
                        {{$index+1}}
                    </div>
                </div>
            </div>
            <div class="own_rank">
                <div style="float:left;width:1.05rem;height:1.05rem;border-radius:50%;overflow:hidden;padding:0;margin:0.1rem 0.2rem 0.1rem 0.63rem;">
                    <img src="https://ss3.bdstatic.com/70cFv8Sh_Q1YnxGkpoWK1HF6hhy/it/u=3583433020,118316633&fm=26&gp=0.jpg" style="width:100%;margin-bottom:100%;display:inline;">
                </div>
                <div style="float:left">
                    <h4 style="font-size:0.3rem;color:#6d7a73;font-weight:normal;margin-top:0.25rem;margin-bottom:0.1rem;">郭郭郭</h4>
                    <p style="font-size:0.26rem;color:#6d7a73;">单词量: <span style="color:#56c499;">0</span></p>
                </div>
                <div style="float:right;text-aligh:right;position:relative;">
                    <div style="width:1.2rem;font-size:0.36rem;color:#fff;background:#37bc85;height:0.7rem;line-height:0.7rem;text-align:center;border-radius:0 0 0.6rem 0.6rem;position:absolute;right:0.3rem;top:0;">29187</div>
                    <p style="font-size:0.24rem;color:#3aba85;margin-top:0.8rem;margin-right:0.4rem;">你的排名</p>
                </div>
            </div>
        </div>
        <mt-actionsheet :actions="actions" v-model="sheetVisible"></mt-actionsheet>
    </div>
</template>

<script>
    import { Actionsheet } from 'mint-ui';
    export default {
        created() {
            setTimeout(()=>{
                var rank = document.querySelector('#rank');
                rank.style.transform = 'translateX(0)';
            },10)
        },
        data(){
            return {
                sheetVisible:false,
                actions:[
                    {
                        name:'微信分享',
                        method:''
                    },
                    {
                        name:'QQ空间',
                        method:''
                    },
                    {
                        name:'微信朋友圈',
                        method:''
                    },
                    {
                        name:'QQ',
                        method:''
                    },
                ],
                arr:[
                    {
                        name:'大脸猫爱心象象',
                        num:'38806'
                    },
                    {
                        name:'goodyy',
                        num:'32604'
                    },
                    {
                        name:'sopaja',
                        num:'32047'
                    },
                    {
                        name:'挥挥手',
                        num:'30458'
                    },
                    {
                        name:'hhha',
                        num:'30212'
                    },
                    {
                        name:'judy',
                        num:'29928'
                    },
                    {
                        name:'klu',
                        num:'29494'
                    },
                    {
                        name:'vivi',
                        num:'28670'
                    },
                    {
                        name:'圆圆圆圆',
                        num:'28566'
                    },
                    {
                        name:'ususus',
                        num:'27983'
                    }
                ]
            }
        }
    }
</script>

<style>
    .cont{
        background: #37bc85;
        padding-top: 0.2rem;
        min-height: 93vh;
        position: relative;
    }
    .own_rank{
        width: 100%;
        height:1.25rem;
        background: #feebc0;
        position: fixed;
        bottom: 0;
    }
</style>



