            </router-link>
        </mt-header>
        <div>
            <div class="kc_book_info_cont" v-for="i in arr" v-if="i.uid==$route.params.uid">
                <div class="kc_book_info_img">
                    <img :src="i.img" alt="">
                </div>
                <h4 style="font-size:0.38rem;line-height:0.6rem;font-weight:normal;padding-left:0.3rem;">{{i.name}}</h4>
                <div style="width:6.8rem;height:0.8rem;line-height:0.8rem;border:0.01rem dotted #000;margin:0 auto;font-size:0.24rem;background:#e5e5e5;">
                    <span style="margin:0 0.3rem;">{{i.keshi}}</span>
                    <span style="color:#808080;">(已经有{{i.num}}人购买)</span>
                    <span style="float:right;margin-right:0.3rem;font-size:0.4rem;color:#ff7420;">{{i.price}}</span>
                </div>
                <dl style="overflow:hidden;font-size:0.28rem;padding-left:0.34rem;line-height:0.6rem;">
