                        name:'6分突破直播课',
                        time:'随时开课',
                        num:'1118',
                        price:'￥34500',
                        img:'http://i1.fuimg.com/699492/2c8a53010f07d551.png',
                        keshi:'140课时',
                        teacher:'雷哥IELTS名师团队'
                    },
                    {
                        uid:'book_3',
                        name:'6.5分提分直播课',
                        time:'随时开课',
                        num:'1077',
                        price:'￥16800',
                        img:'http://i1.fuimg.com/699492/4d6ee84f21e93bc8.png',
                        keshi:'90课时',
                        teacher:'雷哥IELTS名师团队'
                    },
                    {
                        uid:'book_4',
                        name:'7分冲刺直播课',
                        time:'随时开课',
                        num:'846',
                        price:'￥10800',
                        img:'http://i1.fuimg.com/699492/7902496ed8e6ffaf.png',
                        keshi:'60课时',
                        teacher:'雷哥IELTS名师团队'
                        
                    },
                ]
            }
        }
    }
</script>

<style>
    .kc_book_info_img{
        height:4.2rem;
        text-align: center;
        line-height: 4.2rem;
    }
    .kc_book_info_img img{
        width: 3.2rem;
        height:3.6rem;
        vertical-align: middle
    }
</style>



