import Vue from 'vue'
import Router from 'vue-router'
import remember from './routes/remember.vue'
import report from './routes/report.vue'
import circle from './routes/circle.vue'
import shop from './routes/shop.vue'
import re_track from './routes/re_track.vue'
import re_form from './routes/re_form.vue'
import re_assess from './routes/re_assess.vue'
import own_center from './routes/own_center.vue'
import word_save from './routes/word_save.vue'
import plan from './routes/plan.vue'
import mes from './routes/mes.vue'
import voice from './routes/voice.vue'
import cir_act from './routes/cir_act.vue'
import cir_pk from './routes/cir_pk.vue'
import cir_group from './routes/cir_group.vue'
import kc_book_more from './routes/kc_book_more.vue'
import hh_english_book_more from './routes/hh_english_book_more.vue'
import hh_bkgj_more from './routes/hh_bkgj_more.vue'
import hh_xxgj_more from './routes/hh_xxgj_more.vue'
import rank from './routes/rank.vue'
import kc_book_info from './routes/kc_book_info.vue'
import kc_teacher_info from './routes/kc_teacher_info.vue'
import kc_school_info from './routes/kc_school_info.vue'
import study_target from './routes/study_target.vue'
import alarm from './routes/alarm.vue'
import voice_switch from './routes/voice_switch.vue'
Vue.use(Router)

var routes = [
	{
		path:'/',
		name:'remember',
		component:remember
	},
	{
		path:'/report',
		name:'report',
		component:report,
		children:[
			{
				path:'/',
				name:'re_track',
				component:re_track
			},
			{
				path:'re_form',
				name:'re_form',
				component:re_form
			},
			{
				path:'re_assess',
				name:'re_assess',
				component:re_assess
			}
		]
	},
	{
		path:'/circle',
		name:'circle',
		component:circle
	},
	{
		path:'/shop',
		name:'shop',
		component:shop
	},
	{
		path:'/own_center',
		name:'own_center',
		component:own_center
	},
	{
		path:'/word_save',
		name:'word_save',
		component:word_save
	},
	{
		path:'/plan',
		name:'plan',
		component:plan
	},
	{
		path:'/mes',
		name:'mes',
		component:mes
	},
	{
		path:'/voice',
		name:'voice',
		component:voice
	},
	{
		path:'/cir_act',
		name:'cir_act',
		component:cir_act
	},
	{
		path:'/cir_pk',
		name:'cir_pk',
		component:cir_pk
	},
	{
		path:'/cir_group',
		name:'cir_group',
		component:cir_group
	},
	{
		path:'/kc_book_more',
		name:'kc_book_more',
		component:kc_book_more
	},
	{
		path:'/hh_english_book_more',
		name:'hh_english_book_more',
		component:hh_english_book_more
	},
	{
		path:'/hh_bkgj_more',
		name:'hh_bkgj_more',
		component:hh_bkgj_more
	},
	{
		path:'/hh_xxgj_more',
		name:'hh_xxgj_more',
		component:hh_xxgj_more
	},
	{
		path:'/rank',
		name:'rank',
		component:rank
	},
	{
		path:'/kc_book_info/:uid',
		name:'kc_book_info',
		component:kc_book_info
	},
	{
		path:'/kc_teacher_info/:uid',
		name:'kc_teacher_info',
		component:kc_teacher_info
	},
	{
		path:'/kc_school_info/:uid',
		name:'kc_school_info',
		component:kc_school_info
	},
	{
		path:'/study_target',
		name:'study_target',
		component:study_target
	},
	{
		path:'/study_target',
		name:'study_target',
		component:study_target
	},
	{
		path:'/alarm',
		name:'alarm',
		component:alarm
	},
	{
		path:'/voice_switch',
		name:'voice_switch',
		component:voice_switch
	}
	
]
export default new Router({
	routes
})


