<template>
    <div id="voice_switch" style="transform:translateX(100%);transition:.3s;">
        <mt-header title="音效开关" style="height:0.88rem;background: #4cc494;font-size:0.38rem;">
            <router-link to="/own_center" slot="left">
                <mt-button icon="back"></mt-button>
            </router-link>
        </mt-header>
        <div>
            <ul>
                <li class="vw_li">
                    首页音效
                    <mt-switch v-model="value1"></mt-switch>
                </li>
                <li class="vw_li">
                    背单词音效
                    <mt-switch v-model="value2"></mt-switch>
                </li>
                <li class="vw_li">
                    单词评估音效
                    <mt-switch v-model="value3"></mt-switch>
                </li>
                <li class="vw_li">
                    PK结果音效
                    <mt-switch v-model="value4"></mt-switch>
                </li>
                <li class="vw_li">
                    PK背景音效
                    <mt-switch v-model="value5"></mt-switch>
                </li>
                <li class="vw_li">
                    单词自动发音
                    <mt-switch v-model="value6"></mt-switch>
                </li>
            </ul>
        </div>
    </div>
</template>

<script>
    import { Switch } from 'mint-ui';
    export default {
         created() {
            setTimeout(()=>{
                var voice_switch = document.querySelector('#voice_switch');
                voice_switch.style.transform = 'translateX(0)';
            },10)
        },
        data(){
            return {
                value1:true,
                value2:true,
                value3:false,
                value4:false,
                value5:true,
                value6:true,

            }
        }
    }
</script>

<style>
    .vw_li{
        height: 1rem;
        font-size:0.34rem;
        line-height: 1rem;
        padding-left: 0.2rem;
        border-bottom: 0.01rem solid #ddd;
    }
    .mint-switch{
        float: right;
        margin-top:0.14rem;
    }
    .mint-switch-input:checked + .mint-switch-core{
        background: #4cc494;
    }
</style>


