<template>
    <div id="study_target" style="transform:translateX(100%);transition:.3s;min-height:100vh;background:#30c699;">
        <div style="width:100%;height:3.2rem;overflow:hidden;font-size:0;">
            <img src="http://i2.tiimg.com/699492/bbbef8dc33383b4b.png" style="width:100%;">
        </div>
        <div>
            <div style="width:6.85rem;height:10.8rem;background:#fff;border-radius:0.1rem;margin:0.25rem auto;">
                 <h4 style="font-size:0.46rem;text-align:center;line-height:2.15rem;margin-bottom:0.57rem;">学习目标</h4>
                 <ul style="overflow:hidden;">
                     <li v-for="(i,$index) in arr" @click="index=$index" class="target_Li" :class="index==$index?'target_li_active':''">{{i}}</li>
                 </ul>
                 <div class="target_btn">
                     开始学习
                 </div>
            </div>
        </div>
        <div class="iconfont target_back">
            <a href="#/own_center">&#xe74a;</a>
        </div>
    </div>
</template>

<script>
    export default {
         created() {
            setTimeout(()=>{
                var study_target = document.querySelector('#study_target');
                study_target.style.transform = 'translateX(0)';
            },10)
        },
        data(){
            return {
                index:'0',
                arr:['四级','六级','考研','GRE','托福','雅思','GMAT','生活娱乐','旅游天地','中高考','SAT','新概念']
            }
        }
    }
</script>

<style>
    .target_Li{
        float: left;
        width:1.68rem;
        height:0.76rem;
        font-size:0.31rem;
        color:#686868;
        text-align: center;
        margin:0.35rem 0.3rem;
        border:0.02rem solid #acacac;
        border-radius:0.36rem;
        box-sizing: border-box;
        line-height: 0.76rem;
    }
    .target_li_active{
        background: #31c287;
        color:#fff;
        border-color:#31c287;
    }
    .target_btn{
        width:6rem;
        height:1rem;
        background:#31c287;
        color:#fff;
        line-height:1rem;
        text-align:center;
        font-size:0.36rem;
        margin:0.45rem auto;
        border-radius: 0.5rem;
    }
    .target_back{
        width:1rem;
        height: 1rem;
        position: fixed;
        top: 0;
        left:0.1rem;
        font-size:0.7rem;
        text-align: center;
        line-height: 1rem;
        color: #fff;
    }
    .target_back a{
        color:#fff;
    }
</style>


