<template>
    <div id="hh_bkgj_more" style="transform:translateX(100%);transition:.3s;">
        <mt-header title="备考工具" style="height:0.88rem;background: #4cc494;font-size:0.38rem;">
            <router-link to="/shop" slot="left">
                <mt-button icon="back"></mt-button>
            </router-link>
        </mt-header>
        <div class="english_books">
            <div style="overflow:hidden;width:100%;">
                <div v-for="i in hh_bkgj" class="english_gj">
                    <div>
                        <img :src="i.img" alt="">
                    </div>
                    <h4>{{i.name}}</h4>
                    <p>{{i.price}}</p>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
    export default {
        created() {
            setTimeout(()=>{
                var hh_bkgj_more = document.querySelector('#hh_bkgj_more');
                hh_bkgj_more.style.transform = 'translateX(0)';
            },10)
        },
        data(){
            return {
                hh_bkgj:[
					{
						img:'http://i1.fuimg.com/699492/24e0a58baecba7d5.png',
						name:'四六级历年真题合集云盘',
						price:'￥19'
					},
					{
						img:'http://i1.fuimg.com/699492/b325ff6a3f4cae12.png',
						name:'GMAT2020（综合+语文+数学）',
						price:'￥599'
					},
					{
						img:'http://i1.fuimg.com/699492/c4e4d72dca4b93b4.png',
						name:'雅思高频真词本',
						price:'￥9'
					}
				]
            }
        }
    }
</script>

<style>
    .english_books{
        width: 100%;
    }
	.english_books h3 span{
		float: right;
		font-size: 0.26rem;
	}
    .english_gj{
		width:3.42rem;
		float: left;
		margin:0.2rem 0.16rem;
	}
	.english_gj div{
		width:3.42rem;
		height:3.80rem;
		text-align: center;
		line-height: 3.80rem;
		background: #f2f2f2;
		border-radius: 0.3rem;
	} 
	.english_gj div img{
		width:2.75rem;
		height:3.13rem;
		margin-top: 0.35rem;
	}
	.english_gj h4{
		font-weight: normal;
		font-size: 0.34rem;
		line-height: 0.54rem;
		margin-top: 0.13rem;
	}
	.english_gj p{
		font-size: 0.26rem;
		color:#fb8542;
		line-height: 0.43rem;
		margin-bottom: 0.4rem;
	}
</style>


