                        <div :style="{background:i.color}" class="word_icon iconfont" v-html="i.icon"></div>
                        <p style="font-size:0.26rem;color:#5e5e5e;">{{i.name}}</p>
                    </div>
                </mt-tab-container-item>
            </mt-tab-container>
        </div>
    </div>
</template>

