                        type:'出国留学'
                    },
                    {
                        icon:'&#xe69e;',
                        name:'GMAT',
                        color:'#ffcc3f',
                        type:'出国留学'
                    },
                    {
                        icon:'&#xe635;',
                        name:'GRE',
                        color:'#5cb2fd',
                        type:'出国留学'
                    },
                    {
                        icon:'&#xe608;',
                        name:'ACT',
                        color:'#fd9242',
                        type:'出国留学'
                    },
                    {
                        icon:'&#xe629;',
                        name:'IELTS',
                        color:'#002e62',
                        type:'出国留学'
                    },
                    {
                        icon:'&#xe605;',
                        name:'大学英语四级',
                        color:'#6877f4',
                        type:'国内考试'
                    },
                    {
                        icon:'&#xe601;',
                        name:'大学英语六级',
                        color:'#7954ea',
                        type:'国内考试'
                    },
                    {
                        icon:'&#xe610;',
