                        type:'国内考试'
                    },
                    {
                        icon:'&#xe696;',
                        name:'新概念英语',
                        color:'#a46cf5',
                        type:'国内考试'
                    },
                    {
                        icon:'&#xe6ba;',
                        name:'MBA联考',
                        color:'#4a8ad1',
                        type:'国内考试'
                    },
                    {
