<style>
    .word_active{
        color:#37bc85;
        border-bottom: 0.03rem solid #37bc85;
    }
    .word_icon{
        width:1rem;
        height:1rem;
        line-height: 1rem;
        text-align: center;
        color:#fff;
        border-radius: 50%;
        margin:0.2rem auto;
        font-size: 0.6rem;
    }
</style>


