    .word_active{
        color:#37bc85;
        border-bottom: 0.03rem solid #37bc85;
    }
    .plan_box{
        width:1.73rem;
        height:2.2rem;
        float:left;
        margin:0.38rem;
        font-size: 0.24rem;
        border-radius: 0.2rem;
        text-align: center;
    }
    .plan_box:nth-of-type(1n){
        background: #ef915e;
    }
    .plan_box:nth-of-type(2n){
        background: #60cfd6;
    }
    .plan_box:nth-of-type(3n){
        background: #a28ae8;
    }
    .plan_box div{
        width: 1.4rem;
        height:1.3rem;
        margin:0.28rem auto;
        background: rgba(0,0,0,.3);
        border-radius: 0.1rem;
        color:#fff;
        padding-top: 0.3rem;
    }
</style>


