<template>
    <div id="kc_book_more" style="transform:translateX(100%);transition:.3s;">
        <mt-header title="雷哥课程">
            <router-link to="/shop" slot="left">
                <mt-button icon="back"></mt-button>
            </router-link>
            <mt-button icon="more" slot="right"></mt-button>
        </mt-header>
        雷哥课程
    </div>
</template>

<script>
    export default {
        created() {
            setTimeout(()=>{
                var kc_book_more = document.querySelector('#kc_book_more');
                kc_book_more.style.transform = 'translateX(0)';
            },10)
        },
    }
</script>

<style>
    
</style>


